import React from 'react'
import SectionTitle from '../../components/Site/SectionTitle.js'


const Inputs = () => (
        <main id='Principles' className="ph3 ph5-ns pt3">
        <SectionTitle Number='09' Title='Inputs'/>
      <form className="pa4 black-80">
        <div className="measure">
          
          <div>
          <label htmlFor="name" className="f6 fw5 b db mb2 secondary">Name <span className="normal hint">(optional)</span></label>
              <input className='w-40-ns mb4 mb0-ns w-100 bg-transparent' required/>
              </div>

              <div className='mt5'>
              <label htmlFor="name" className="f6 fw5 b db mb2 secondary">Textarea</label>
              <textarea rows="3" className="db w-100 mt4 bg-transparent pointer" name="message" required/>
          </div>
        </div>



      </form>
      </main>

)



export default Inputs



