import React from 'react'
import SectionTitle from '../../components/Site/SectionTitle.js'


const Vision = () => (
        <main id='Vision' className="ph3 ph5-ns pt5">
        <SectionTitle Number='02' Title='Vision'/>
      	<p className='lh-copy secondary mw7'> Typography has one plain duty before it and that is to convey information in writing. No argument or consideration can absolve typography from this duty. A printed work which cannot be read becomes a product without purpose.</p>
      </main>

)

export default Vision



