import Buttons from '../components/System/Buttons.js'
import Inputs from '../components/System/Inputs.js'
import DropDowns from '../components/System/DropDowns.js'
import List from '../components/System/List.js'

import Footer from '../components/System/Footer.js'


const IndexPage = () => (

<div >
  <header>
    <div className="cover bg-left bg-center-l overflow-auto relative">
      <img className='w-100' src='../img/header.svg'/>
    </div> 
  </header>
<div>
    <div className='w-80-ns w-100 fr dib v-top'>
    <Principles/>
    <Vision/>
    <Logo/>
    <Colors/>
    <Typography/>
    <Iconography/>
    <Illustrations/>
    <Buttons/>
    <Inputs/>
    <DropDowns/>
    <List/>
    <Footer/>

    </div>
  </div>
  </div>
)

export default IndexPage



