import React from 'react'
import SectionTitle from '../../components/Site/SectionTitle.js'


const Buttons = () => (
        <main id='Principles' className="ph3 ph5-ns pt3 mb5">
        <SectionTitle Number='08' Title='Buttons'/>
        <h4 className='mt5'>Primary action buttons</h4>
		<a style={{letterSpacing: '0.75px'}} className="f6 link dim br2 ph3 pv2 fw4
		 mb2 dib white bg-brand ttu" href="#0">Primary Active</a>
		<a className="f6 link dim br2 ph3 pv2 fw4 mb2 dib white bg-disabled ttu ml4" href="#0">Primary disabled</a>
		<h4 className='mt5'>Secondary action buttons</h4>
		<a className="f6 link dim br2 ph3 pv2 fw4 mb2 dib link-active bg-transprent ttu" href="#0">Secondary Active</a>
		<a className="f6 link dim br2 ph3 pv2 fw4 mb2 dib link-disabled ttu ml4" href="#0">Primary disabled</a>
		<a className="f6 link dim br2 ph3 pv2 fw4 mb2 dib alert ttu ml4" href="#0">destructive action</a>

		<h4 className='mt5'> buttons</h4>
		<a className="f6 link dim br2 ph3 pv2 fw4 mb2 dib bg-tertiary ttu primary" href="#0">Secondary Active</a>

      </main>

)



export default Buttons



